.modTab02 {
    @extend .comTabData;

    .tabMenuGroup {
        .tabMenu.auto {
            flex-wrap: wrap; gap: 12px;
        }
    }
    .tabContianer {
        margin-top: 24px;
        .tabCont {display:none;}
        .tabCont.on {display:block;}        
        &.mgH40 {margin-top: 40px;}
    }

    @include media('mobile'){
        .tabMenuGroup {
            .tabMenu.auto {
                gap: 8px;
                .tabItem {
                    .tabName {
                        height: 36px; line-height: 36px;
                    }
                }
            }
        }
        .tabContianer {
            width: 100%; padding: 0 16px;
        }
    }

    //  
    .comTitH2 + & {margin-top: 32px;}
}